// src/App.js
import React, { useEffect, useState } from "react";
import "./App.css";
import { computeWeightedStats, recommendHand } from "./algorithms";
import { loadRecords, saveRecord as saveRecordToStore, deleteRecord } from "./db";
import CumulativeChart from "./components/CumulativeChart";

const HANDS = [
  { id: "rock", emoji: "", label: "" },
  { id: "scissors", emoji: "", label: "" },
  { id: "paper", emoji: "", label: "" },
];

const DEFAULTS = {
  recencySensitivity: 0.4,
  expectedVsWinRate: 0.5,
  filterMode: "All",
  expectationLine: 23.33,
  chartMaxY: 40,
};

export default function App() {
  const [tab, setTab] = useState("input");
  const [records, setRecords] = useState([]);
  const [date, setDate] = useState(new Date().toISOString().slice(0, 10));
  const [selectedHand, setSelectedHand] = useState(null);
  const [result, setResult] = useState("draw");
  const [recencySensitivity, setRecencySensitivity] = useState(DEFAULTS.recencySensitivity);
  const [expectedVsWinRate, setExpectedVsWinRate] = useState(DEFAULTS.expectedVsWinRate);
  const [saving, setSaving] = useState(false);
  const [showRecencyInfo, setShowRecencyInfo] = useState(false);
  const [showExpectedInfo, setShowExpectedInfo] = useState(false);
  const [filterMode, setFilterMode] = useState(DEFAULTS.filterMode);

  useEffect(() => {
    (async () => {
      try {
        const rs = await loadRecords();
        setRecords(rs || []);
      } catch (e) {
        console.error("loadRecords failed", e);
        setRecords([]);
      }
    })();
  }, []);

  const filteredRecords = (() => {
    if (!records || records.length === 0) return [];
    if (filterMode === "All") return records;
    const n = filterMode === "Last10" ? 10 : filterMode === "Last30" ? 30 : 50;
    return records.slice(-n);
  })();

  // helpers
  const fmtTwo = (v) => {
    if (v == null) return "0.00";
    const n = Number(v);
    if (!Number.isFinite(n)) return "0.00";
    return n.toFixed(2);
  };
  const fmtInt = (v) => {
    if (v == null) return "0";
    const n = Number(v);
    if (!Number.isFinite(n)) return "0";
    return String(Math.round(n));
  };

  const validScores = (filteredRecords || []).map(r => Number(r.score)).filter(v => Number.isFinite(v));
  const averageScore = validScores.length
    ? (validScores.reduce((a, b) => a + b, 0) / validScores.length).toFixed(2)
    : "0.00";

  const stats = computeWeightedStats(filteredRecords, new Date(date), recencySensitivity);
  const recommendedHand = recommendHand(stats, expectedVsWinRate);
  const finalScore = Number.isFinite(stats[recommendedHand]?.avgScore)
    ? Math.round(stats[recommendedHand].avgScore)
    : 0;

  useEffect(() => {
    if (!selectedHand && recommendedHand) {
      setSelectedHand(recommendedHand);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [filteredRecords, recommendedHand]);

  const refresh = async () => {
    try {
      const rs = await loadRecords();
      setRecords(rs || []);
      return rs || [];
    } catch (e) {
      console.error("refresh failed", e);
      return [];
    }
  };

  // STRONG FIX: save only after confirming persistence.
  const onSave = async () => {
    if (saving) return;
    if (!selectedHand) {
      alert("");
      return;
    }
    setSaving(true);
    const score = result === "win" ? 40 : result === "draw" ? 20 : 10;
    const record = {
      id: Date.now().toString(),
      date,
      hand: selectedHand,
      result,
      score,
    };

    try {
      // 1) try to save to backend / storage
      const ok = await saveRecordToStore(record);
      // saveRecordToStore should return true on success, false on known-failure, or throw on error.
      if (ok === false) throw new Error("saveRecord returned false");

      // 2) refresh and verify the record exists in the authoritative source
      const rs = await refresh();
      const found = (rs || []).some(r => String(r.id) === String(record.id) || (r.date === record.date && r.hand === record.hand && Number(r.score) === Number(record.score)));
      if (!found) {
        // If not found, attempt one more time to fetch/insert: (some backends assign server ids)
        // try to match by date+hand+score as fallback
        // After retry, if still missing, inform user and keep UI consistent by reloading authoritative data.
        const rs2 = await refresh();
        const found2 = (rs2 || []).some(r => (r.date === record.date && r.hand === record.hand && Number(r.score) === Number(record.score)));
        if (!found2) {
          alert(" Supabase ");
          return;
        }
      }

      // Success: tell user and refresh UI from authoritative data
      await refresh();
      alert("");
    } catch (e) {
      console.error("saveRecord failed:", e);
      alert("");
    } finally {
      setSaving(false);
    }
  };

  const onDelete = async (id) => {
    if (!window.confirm("")) return;
    try {
      await deleteRecord(id);
      await refresh();
    } catch (e) {
      console.error("delete failed", e);
      alert("");
    }
  };

  const onBack = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  // reset UI to defaults
  const resetUI = () => {
    setRecencySensitivity(DEFAULTS.recencySensitivity);
    setExpectedVsWinRate(DEFAULTS.expectedVsWinRate);
    setFilterMode(DEFAULTS.filterMode);
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  return (
    <div className="app">
      <header className="app-header">
        <h1 className="title"></h1>
        <div className="tab-buttons">
          <button className={`tab ${tab === "input" ? "active" : ""}`} onClick={() => setTab("input")}></button>
          <button className={`tab ${tab === "stats" ? "active" : ""}`} onClick={() => setTab("stats")}></button>
        </div>
      </header>

      <main className="main">
        <section className="top-row">
          <div className="avg-card">
            <div className="avg-label"></div>
            <div className="avg-number">{averageScore}</div>
          </div>

          <div className="filter-row">
            <label className="filter-label">Filter</label>
            <select className="filter-select" value={filterMode} onChange={e => setFilterMode(e.target.value)}>
              <option value="All">All</option>
              <option value="Last10">Last 10</option>
              <option value="Last30">Last 30</option>
              <option value="Last50">Last 50</option>
            </select>
            <button className="reset-ui-btn" onClick={resetUI}>Reset</button>
          </div>
        </section>

        <section className="controls">
          <div className="control-item control-with-legend">
            <div className="control-head" style={{display:"flex", alignItems:"center", gap:8}}>
              <label style={{marginRight:8}}>Recency</label>
              <button className="info-btn" onClick={() => setShowRecencyInfo(s => !s)}>i</button>
              <div className="legend" style={{fontSize:12, marginLeft:8}}>0    1  </div>
            </div>
            <input type="range" min="0" max="1" step="0.01" value={recencySensitivity}
              onChange={e => setRecencySensitivity(Number(e.target.value))}/>
            {showRecencyInfo && <div className="tooltip"></div>}
          </div>

          <div className="control-item control-with-legend">
            <div className="control-head" style={{display:"flex", alignItems:"center", gap:8}}>
              <label style={{marginRight:8}}>Expected</label>
              <button className="info-btn" onClick={() => setShowExpectedInfo(s => !s)}>i</button>
              <div className="legend" style={{fontSize:12, marginLeft:8}}>0    1  </div>
            </div>
            <input type="range" min="0" max="1" step="0.01" value={expectedVsWinRate}
              onChange={e => setExpectedVsWinRate(Number(e.target.value))}/>
            {showExpectedInfo && <div className="tooltip"></div>}
          </div>

          <div className="date-row">
            <label></label>
            <input type="date" value={date} onChange={e => setDate(e.target.value)} />
          </div>
        </section>

        {tab === "input" && (
          <section className="input-section">
            <div className="recommendation-row">
              <div className="rec-label">Recommendation</div>
              <div className="rec-pill">
                <div className="rec-emoji">{recommendedHand ? (recommendedHand === 'rock' ? '' : recommendedHand === 'scissors' ? '' : '') : ''}</div>
                <div className="rec-text">{recommendedHand ? (recommendedHand === 'rock' ? '' : recommendedHand === 'scissors' ? '' : '') : ''}</div>
                <div className="rec-score">FinalScore: {finalScore}</div>
              </div>
            </div>

            <div className="hand-cards" style={{overflow:"visible"}}>
              {HANDS.map(h => {
                const s = stats[h.id] || {};
                return (
                  <div
                    key={h.id}
                    className={`hand-card ${selectedHand === h.id ? "selected" : ""}`}
                    onClick={() => setSelectedHand(h.id)}
                    style={{overflow:"visible"}}
                  >
                    <div className="hand-emoji">{h.emoji}</div>
                    <div className="hand-text">{h.label}</div>
                    <div className="hand-sub">
                      <div className="plays-label">plays: <strong>{fmtInt(s.plays)}</strong></div>
                      <div className="avg-label">avg: <strong>{s.avgScore ? fmtTwo(s.avgScore) : "0.00"}</strong></div>
                    </div>
                  </div>
                );
              })}
            </div>

            <div className="result-row">
              <button className={`pill ${result === "win" ? "active" : ""}`} onClick={() => setResult("win")}></button>
              <button className={`pill ${result === "draw" ? "active" : ""}`} onClick={() => setResult("draw")}></button>
              <button className={`pill ${result === "loss" ? "active" : ""}`} onClick={() => setResult("loss")}></button>
            </div>

            <div className="actions-row">
              <button className="save-btn" onClick={onSave} disabled={saving}>
                {saving ? "" : ""}
              </button>
            </div>

            <div className="back-row">
              <button className="back-btn" onClick={onBack}></button>
            </div>

            <div id="history" className="history-section">
              <table className="history-table" role="table">
                <thead>
                  <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  {filteredRecords.slice().reverse().map(r => (
                    <tr key={r.id}>
                      <td>{r.date}</td>
                      <td className="td-hand">{r.hand ? (r.hand === 'rock' ? '' : r.hand === 'scissors' ? '' : '') : ''}</td>
                      <td>{r.result === 'win' ? '' : r.result === 'draw' ? '' : r.result === 'loss' ? '' : r.result}</td>
                      <td>{Number(r.score)}</td>
                      <td><button className="small-btn" onClick={() => onDelete(r.id)}></button></td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </section>
        )}

        {tab === "stats" && (
          <section className="stats-section">
            <h2></h2>
            <div className="chart-wrap">
              <div className="chart-inner">
                <CumulativeChart records={records} />
              </div>
            </div>
            {/* removed hand-summary-cards per request */}
          </section>
        )}
      </main>

      <footer className="app-footer">
        App version: stylish-ui-restore
      </footer>
    </div>
  );
}
