// src/components/CumulativeChart.jsx
import React, { useMemo, useRef } from "react";
import { Line } from "react-chartjs-2";
import {
  Chart as ChartJS,
  LineElement,
  PointElement,
  CategoryScale,
  LinearScale,
  Tooltip,
} from "chart.js";

ChartJS.register(LineElement, PointElement, CategoryScale, LinearScale, Tooltip);

function formatDateLabel(dateStr){
  try{
    const d = new Date(dateStr);
    if (isNaN(d)) return dateStr;
    return `${d.getMonth()+1}/${d.getDate()}`;
  }catch(e){ return dateStr; }
}

export default function CumulativeChart({ records = [] }) {
  const chartRef = useRef(null);

  const { labels, cumAverages } = useMemo(() => {
    // group by date (yyyy-mm-dd)
    const byDate = {};
    records.forEach(r => {
      if (!r) return;
      const dRaw = r.created_at || r.date || r.day || r.createdAt;
      if (!dRaw) return;
      const iso = (new Date(dRaw)).toISOString().slice(0,10);
      const s = Number(r.score ?? r.points ?? r.value ?? 0);
      if (!byDate[iso]) byDate[iso] = { sum: 0, count: 0 };
      if (!Number.isNaN(s)) { byDate[iso].sum += s; byDate[iso].count += 1; }
    });
    const dates = Object.keys(byDate).sort();
    const dailyAvg = dates.map(d => {
      const o = byDate[d];
      return o.count > 0 ? (o.sum / o.count) : 0;
    });

    // cumulative average up to each date
    const cum = [];
    let runningSum = 0;
    let runningCount = 0;
    for (let i=0;i<dailyAvg.length;i++){
      runningSum += dailyAvg[i];
      runningCount += 1;
      const ca = runningCount > 0 ? (runningSum / runningCount) : 0;
      cum.push(Number(ca.toFixed(2)));
    }

    return { labels: dates, cumAverages: cum };
  }, [records]);

  const data = useMemo(() => ({
    labels: labels.map(l => formatDateLabel(l)),
    datasets: [
      {
        label: "",
        data: cumAverages,
        borderColor: "#1f6ef8",
        backgroundColor: "#1f6ef8",
        pointRadius: 6,
        pointHoverRadius: 9,
        pointHitRadius: 16, // easier to tap
        tension: 0.2,
      },
      {
        label: "",
        data: labels.map(()=>23.33),
        borderColor: "#d94b4b",
        borderDash: [6,6],
        pointRadius: 0,
        fill: false,
      }
    ]
  }), [labels, cumAverages]);

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    interaction: {
      mode: 'nearest',
      intersect: false, // allow near clicks/taps
    },
    plugins: {
      tooltip: {
        enabled: true,
        callbacks: {
          label: function(context) {
            const v = context.parsed.y;
            return `${context.dataset.label || ''}: ${Number(v).toFixed(2)}`;
          }
        }
      },
      legend: { display: false },
    },
    scales: {
      y: {
        suggestedMin: 0,
        suggestedMax: 40,
        ticks: { stepSize: 5 },
      },
      x: {
        ticks: {
          maxRotation: 0,
          minRotation: 0,
          autoSkip: true,
          maxTicksLimit: 8,
        }
      }
    },
    onClick: (evt, elements) => {
      const chart = chartRef.current;
      if (!chart) return;
      // elements param sometimes contains points; if empty, try nearest
      const pts = elements && elements.length ? elements : chart.getElementsAtEventForMode(evt.native, 'nearest', {intersect:false}, false);
      if (pts && pts.length) {
        const first = pts[0];
        // show tooltip at that point
        chart.tooltip.setActiveElements([{ datasetIndex:first.datasetIndex, index:first.index }], {x:first.x, y:first.y});
        chart.update();
      }
    }
  };

  return (
    <div style={{width:"100%", overflowX:"auto"}}>
      <div style={{minWidth: Math.max(480, labels.length * 70) + "px", height: "260px", paddingBottom: "8px"}}>
        <Line ref={chartRef} data={data} options={options} />
      </div>
    </div>
  );
}
